import Swiper, { Autoplay, FreeMode } from 'swiper';

let SwiperTop = new Swiper('#marquee_top', {
    modules: [Autoplay, FreeMode],
    spaceBetween: 0,
    centeredSlides: true,
    speed: 2500,
    freeMode: true,
    autoplay: {
        delay: 0,
        pauseOnMouseEnter: true,
        disableOnInteraction: false
    },
    loop: true,
    slidesPerView: 'auto',
});


let SwiperBottom = new Swiper('#marquee_bottom', {
    modules: [Autoplay],
    spaceBetween: 0,
    centeredSlides: true,
    speed: 2500,
    autoplay: {
        delay: 1,
        reverseDirection: true,
        pauseOnMouseEnter: true,
        disableOnInteraction: false
    },
    loop: true,
    slidesPerView: 'auto',
});

